import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
// import './../Scene/Contents.css'

const useStyles = makeStyles((theme) => ({
    root: {
        position: "fixed",
        backgroundColor: '#FFFFFF88',
        width: '100%',
        height: '65px',
        bottom: '0px',
        left: '0%'
    }
}));

export const RewardButton = ({ onClick, rewardCategory, rewardExp }) => {
    const classes = useStyles(); 

    const getRewardIconPath = () => {
        //console.log('=====rewardCategory:'+ rewardCategory);
        switch (rewardCategory) {
            case 'strength': return 'https://goli8111.github.io/level-up/images/card_assets/icon_power.png';
            case 'dexterity': return 'https://goli8111.github.io/level-up/images/card_assets/icon_knowledge.png';
            case 'intelligence': return 'https://goli8111.github.io/level-up/images/card_assets/icon_skill.png';
            case 'wisdom': return 'https://goli8111.github.io/level-up/images/card_assets/icon_wisdom.png';
        }
    }

    return (
        <div className={classes.root}>
            <div onClick={onClick} className='quest_reward_main_button'>
                <span>  </span>
            </div>
            <div onClick={onClick} className='quest_reward_button'>
                <img src={getRewardIconPath()}></img>
                <span >{rewardExp}</span>
            </div>
        </div>
    );
}

export default RewardButton;
