import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import { setScene } from '../actions/scene';
import { InvalidPosenetClear } from '../actions/posenet';

import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';

import VideoPlay from '../components/videoPlay'
import RewardButton from './../components/RewardButton';
import Button from '@material-ui/core/Button';
import { _contents } from './ContentsData'
import { GetIframeReward, SetIframeFinish, SetIframeFinishAndLeave } from './../components/utils/SendMessage';
import './Contents.css';
import { GetVideoListFromFullList, GetVideoFullList} from './../components/utils/VideoRecord'
import { playEnableClickSound } from './../components/utils/SoundPlay';
import DefaultConfirmDialog from './../components/DefaultConfirmDialog';

const useStyles = makeStyles((theme) => ({
    root: {
        position: 'relative',
        width: '100%',
    },
    videoContainer: {
        width: '100%',
        position: 'relative',
    },
    contentsTitle: {
        color: 'black'
    },
    BottomBarBack: {
        position: "fixed",
        backgroundColor: '#FFFFFF88',
        width: '100%',
        height: '11%',
        top: '89%',
        left: '0%'
    },
    BottomBarGroup: {
        position: "fixed",
        bottom: "70px",
        width: "100%"
    },

    BottomBarButton: {
        position: 'fixed',
        bottom: '5px',
        left: '0px',
        //right: '5px',
        width: '99%',
        height: '50px',
        backgroundColor: 'orange',
        boxShadow: '3px 3px 1px 1px rgba(0,0,0,0.75)',
        color: 'white'
    },
    BottomBarButton_text_1: {
        position: 'absolute',
        left: "0",
        top: "5%",
        width: "100%",
        fontSize: '20px',
        fontWeight: 'bold'
    },
    BottomBarButton_text_2: {
        position: 'absolute',
        left: "0",
        top: "60%",
        width: "100%",
        fontSize: '12px',
    }
}));


export const IntroScene = ({ _posenetPose, setScene, _refreshIntro, _currentScene,
    rewardState, rewardCategory, rewardExp }) => {
    const classes = useStyles();
    const [_submitConfirm, setSubmitConfirm] = React.useState(false);
    console.log('IntroScene start');

    const createVideoElement = (title, desc, url) => {
        //console.log('createVideoElement ' + title)
        var poster = url.replace(".mp4", ".jpg");
        poster = url.replace(".webm", ".jpg");
        return (
            <div key={title} >
                <span className={classes.contentsTitle}>{desc}</span>
                <div className={classes.videoContainer}>
                    <VideoPlay key={url} title={title} enableContentsSkip={false} enableContentsPause={true} videoUrl={url} autoPlay={false} enableTitleText={false} enableFullScreen={false} enableScreenMirror={false} poster={poster} />
                </div>
            </div>);
    }

    const getContents = () => {
        let contents = [];
        Object.keys(_contents).forEach(function (k, v) {
            if (_contents[k].type == 'set') {
                var url = _contents[k].url;
                if (window.HybridApp == undefined) {
                  url = _contents[k].web_url;
                }
                contents.push(createVideoElement(_contents[k].title, _contents[k].desc, url));
            }
        });
        return contents;
    }

    const onPlay = () => {
        console.log('onPlay call');
        playEnableClickSound();
        setScene('Play');
        //setScene('BodyScan');
    }

    const onGetRewardClick = () => {
        console.log("onGetReward call");
        GetIframeReward();
    }

    const onFinishClick = () => {
        console.log("onFinishClick call");
        playEnableClickSound();
        //SetIframeFinish();
        setScene("IntroStampScene");
    }

    const onTempFinish = () => {
        playEnableClickSound();
        SetIframeFinishAndLeave();
    }

    const getMainButton = () => {
        var videoFullList = GetVideoFullList(_contents);
        var invalidCount = GetVideoListFromFullList(videoFullList, false);
        var validCount = GetVideoListFromFullList(videoFullList, true);

        if (true == rewardState) {
            return (<RewardButton onClick={onGetRewardClick} rewardCategory={rewardCategory} rewardExp={rewardExp} ></RewardButton>);
        } else {
            if (_currentScene == 'IntroStampScene') {
                return (
                    <div onClick={onTempFinish} className='quest_main_disable_button'>
                        <span>  </span>
                    </div>
                );
            } else {
                if (invalidCount == 0) {
                    return (
                        <div onClick={onSubmitConfirmOpen} className='quest_main_button'>
                            <span> </span>
                        </div>
                    );
                } else if (validCount > 0) {
                    return (
                        <div onClick={onPlay} className='quest_main_button'>
                            <span></span>
                        </div>
                    );
                }
                return (
                    <div onClick={onPlay} className={classes.BottomBarButton}>
                        <span className={classes.BottomBarButton_text_1}>  </span>
                        <span className={classes.BottomBarButton_text_2}> 11 Set / 15 </span>
                    </div>
                );
            }
        }
    }

    const onSubmitConfirmOpen = () => {
        setSubmitConfirm(true);
        playEnableClickSound();
    }

    const onSubmitConfirmClose = () => {
        setSubmitConfirm(false);
        playEnableClickSound();
    }

    const onSubmitConfirmOK = () => {
        onFinishClick();
        setSubmitConfirm(false);
    }

    return (
        <div className={classes.root}>
            <div className='contents_body'>
                <div className='contents_intro'>
                    <span className='.contents_intro_text'> !</span>
                    <span className='contents_intro_text_sub'><br /> 16    ,   .<br /></span>
                </div>
                {getContents()}
                <br />
                <br />
                <br />
                <div className={classes.BottomBarBack}></div>
            </div>
            <div className={classes.BottomBarGroup}>
                {getMainButton()}
            </div>
            <DefaultConfirmDialog title='      .' onOKClick={onSubmitConfirmOK} onCloseClick={onSubmitConfirmClose} openDialog={_submitConfirm} />
        </div>
    );
}

const mapStateToProps = (state) => ({
    _posenetPose: state.posenet._posenetPose,
    _refreshIntro: state.scene._refreshIntro,
    _currentScene: state.scene._currentScene
});
const mapDispatchToProps = { setScene };

export default connect(mapStateToProps, mapDispatchToProps)(IntroScene);
